import React, { useRef, useState } from 'react'
import { Row, Button } from 'react-bootstrap';
import { Table } from 'react-bootstrap';
import { Col, Form, Modal } from 'react-bootstrap';
import shortid from 'shortid';
import callApi from '../../../helpers/conectorApi';
import { alert_exitoso, alert_warning } from '../../../helpers/Notificacion';
import Loading from './Loading';
import fileDownload from "js-file-download";
export const ImportarDatos = ({ tipo, abrirModal, setAbrirModal, nombrePlantilla }) => {
    const uploadInput = useRef();
    const [loading, setLoading] = useState(false);
    const [actualizarPadre, setActualizarPadre] = useState(false);
    const [enviandoData, setEnviandoData] = useState(false);
    const [verDetalle, setVerDetalle] = useState(false);
    const [listEncabezado, setListEncabezado] = useState([]);
    const [listadoDetalle, setListadoDetalle] = useState([]);

    const handleAuxCerrarModal = () => {
        if (!enviandoData) {
            setAbrirModal(actualizarPadre);
        }
    }

    const handleDescargar = async () => {
        let response = await callApi(`importacion/${tipo}`);
        if (response) {
            let { type, data } = response;
            var arrayBufferView = new Uint8Array(data);
            var blob = new Blob([arrayBufferView], { type: "application/octet-stream" });
            fileDownload(blob, `plantillaPersonas.xlsx`);
        }
    }

    const handleImportar = async () => {
        let file = uploadInput.current.files[0];
        if (file) {
            setLoading(true);
            setEnviandoData(true);
            let fileParts = file.name.split('.');
            let fileName = shortid.generate() + "_" + fileParts[0];
            let fileType = fileParts[1];
            const data = new FormData();
            data.append("file", file, file.name);

            let response = await callApi(`importacion/${tipo}`, {
                method: 'POST',
                body: data
            }, true);
            if (response) {
                let { header, items } = response;
                if (items.length > 0 && header.length > 0) {
                    setListEncabezado(header);
                    setListadoDetalle(items);
                    setVerDetalle(true);
                    setActualizarPadre(true);
                } else {
                    alert_warning("No se agreg informacin valida en el archivo");
                }
            }
            setEnviandoData(false);
            setLoading(false);
        } else {
            alert_warning("Debe de seleccionar un archivo");
        }

    }
    return (
        <Modal show={abrirModal} onHide={() => { handleAuxCerrarModal() }} size="xl">
            <Modal.Header closeButton>
                <Modal.Title as="h5">Importar Datos</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                {
                    loading === true ?
                        <Loading />
                        : !verDetalle ?
                            <>
                                <Row>
                                    <Col sm={12}>
                                        <h5 className="mt-3">Por favor cargar el archivo de Excel, utilizar la plantilla <button type="button" onClick={()=>{handleDescargar()}} className="btn-sm btn-round has-ripple btn btn-primary"><i className="feather icon-download"></i> {nombrePlantilla}</button> </h5>
                                    </Col>
                                </Row>
                                <hr />
                                <Row>
                                    <Col sm={6}>
                                        <div className="form-group fill">
                                            <label className="floating-label" htmlFor="inputPlantilla">Archivo</label>
                                            <input type="file" ref={uploadInput} className="form-control" id="inputPlantilla" placeholder="Cargar archivo" />
                                        </div>
                                    </Col>
                                </Row>
                            </>
                            : <>
                                <Table striped hover responsive bordered id="mytable">
                                    <thead>
                                        <tr>
                                            <th>No.</th>
                                            {
                                                listEncabezado.map((name, index) => {
                                                    return (
                                                        <th key={`th_${index}`}>{name}</th>
                                                    )
                                                })
                                            }
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {
                                            listadoDetalle.map((item, index) => {
                                                let keys = Object.keys(item);
                                                return (
                                                    <tr key={`tr_${index}`}>
                                                        <td>{index + 1}</td>
                                                        {
                                                            keys.map((key, i) => {
                                                                let texto = item[`${key}`];
                                                                if (texto === undefined) {
                                                                    texto = "";
                                                                }
                                                                if (texto === true) {
                                                                    return (
                                                                        <td className="text-center" key={`td_key${i}`}><label className="badge badge-light-success">Ok</label></td>
                                                                    )
                                                                } else {
                                                                    return (
                                                                        <td key={`td_key${i}`}>{texto}</td>
                                                                    )
                                                                }

                                                            })
                                                        }
                                                    </tr>
                                                )
                                            })
                                        }
                                    </tbody>
                                </Table>
                            </>
                }

            </Modal.Body>
            <Modal.Footer>
                <Button variant="danger" onClick={() => handleAuxCerrarModal()}>{!verDetalle ? 'Cancelar' : 'Salir'}</Button>
                {
                    !verDetalle &&
                    <Button variant="primary" onClick={() => handleImportar()}>Importar</Button>
                }
            </Modal.Footer>
        </Modal>
    )
}
